<template>
  <el-container style="height: 100%; border: 1px solid #eee">


    <el-container>
      <el-header style="text-align: right; font-size: 25px; height: 60px;">
        <el-dropdown>
          <i class="el-icon-setting" style="margin-right: 15px"></i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item><el-button type="text" @click="toLogin"></el-button></el-dropdown-item>
            <el-dropdown-item><el-button type="text" @click="showDialog(' mabo ')"></el-button></el-dropdown-item>
            <el-dropdown-item><el-button type="text" @click="showDialog(':2420935619@qq.com')"></el-button></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <span style="margin-right: 30px"> :{{this.name}}</span>
      </el-header>

      <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="90%"
          :before-close="handleClose">
        <span>{{this.msg}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>

      <div>
        <el-button type="primary" style=" height: 40px ;padding: 5px" plain
                   @click="showWitch(1);"></el-button>
        <el-button type="primary" style=" height: 40px ;padding: 5px" plain
                   @click="showWitch(2)"></el-button>
        <el-button type="primary" style=" height: 40px ;padding: 5px" plain
                   @click="showWitch(3)"></el-button>
        <el-button type="primary" style="width: 40%; height: 40px ;padding: 5px" plain
                   @click="showWitch(4)"></el-button>
        <el-button type="primary" style="width: 40%; height: 40px ;padding: 5px" plain
                   @click="showWitch(5)"></el-button>
      </div>
      <!--      -->
      <el-main >

        <el-table
            v-show="this.showTableName===1"
            :data="tableData"
            style="width: 100%">
          <el-table-column label=""  width="120">
            <template slot-scope="scope" >
              <el-button
                  size="mini"
                  width="120"
                  @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
          <el-table-column prop="reportDate" label="" width="120"></el-table-column>
          <el-table-column prop="bodyTemperature" label="" width="120">
            <template scope="scope">
              <span  v-if="scope.row.bodyTemperature>37.3" style="color: red">{{ scope.row.bodyTemperature }}</span>
              <span  v-else >{{ scope.row.bodyTemperature }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="isConfirm" label="" width="120">
            <template scope="scope">
              <span  v-if="scope.row.isConfirm===''" style="background-color: red; color: #d7edff" >{{ scope.row.isConfirm }}</span>
              <span  v-else >{{ scope.row.isConfirm }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="isArea" label="14" width="120">
            <template scope="scope">
              <span  v-if="scope.row.isArea===''" style="background-color: red; color: #d7edff" >{{ scope.row.isArea }}</span>
              <span  v-else >{{ scope.row.isArea }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="address" label=""></el-table-column>
        </el-table>


        <!--      -->
        <el-form :model="ruleForm"
                 v-show="this.showTableName===2"
                 :rules="rules" ref="ruleForm" label-width="30%"  class="demo-ruleForm">
          <el-form-item label="" >
            <el-switch v-model="ruleForm.isConfirm2"></el-switch>
          </el-form-item>
          <el-form-item label="" >
            <el-switch v-model="ruleForm.isArea2"></el-switch>
          </el-form-item>

          <el-form-item label="" prop="bodyTemperature2" >
            <el-input v-model="ruleForm.bodyTemperature2" style="width: 200px"></el-input>
          </el-form-item>

          <el-form-item label="" prop="address2" >
            <el-select v-model="ruleForm.address2" style="width: 200px">
              <el-option v-for="item in this.addresses"
                         :value="item" :key="item">
                {{item}}
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" >
            <el-form-item prop="date2">
              <el-date-picker
                  v-model="ruleForm.date2"
                  type="date"
                  placeholder=""
                  format="yyyy  MM  dd "
                  :disabled="true"
                  value-format="yyyy-MM-dd">
              </el-date-picker>

            </el-form-item>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>

        <!--        -->
        <el-form :model="ruleForm"
                 style="width: 300px;"
                 v-show="this.showTableName===3">
          <el-form-item label="" >
            <el-date-picker
                v-model="userBase.birthday"
                :disabled="displayUserBase"
                style="width: 220px; "
                type="date"
                placeholder=""
                format="yyyy  MM  dd "
                value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="userBase.name"
                      :disabled="displayUserBase">
            </el-input>
          </el-form-item >

          <el-form-item label="">
            <el-input @change="isvalidPhone(userBase.phone)" v-model="userBase.phone"
                      :disabled="displayUserBase">
            </el-input>
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="userBase.sex"  :disabled="displayUserBase">
              <el-option v-for="item in this.sexs"
                         :value="item" :key="item">
                {{item}}
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input @change="isCardID(userBase.idCardNumber)" v-model="userBase.idCardNumber"
                      :disabled="displayUserBase">
            </el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="userBase.email"
                      :disabled="true">
            </el-input>
          </el-form-item>
          <el-form-item :disabled=true>
            <el-button type="primary" v-show="!displayUserBase" @click="submitUserBaseInfo(userBase)"></el-button>
            <el-button type="primary" v-show="displayUserBase" @click="displayUserBase=false"></el-button>
          </el-form-item>
        </el-form>
<!--        -->
        <div  v-show="this.showTableName===4||this.showTableName===5">
          <div style="margin-bottom: 9%;">
            <el-date-picker
                @change="datePick()"
                v-model="reportDate"
                type="date"
                format="yyyy  MM  dd "
                value-format="yyyy-MM-dd"
            >
            </el-date-picker>
          </div>
        </div>

        <div v-show="this.showTableName===4">
          <span style="display: flex;margin-bottom: 10px">
            <el-button style="height: 40px;margin-right : 10%"
                       type="primary" @click="directToClassInfo('','')">{{todayStudentReportInfoYes.length}}
            </el-button>

            <el-progress :stroke-width="7"
                         type="circle"
                         :width="70"
                         :height="70"
                         style="margin-right: 10%"
                         v-if="parseInt(100*todayStudentReportInfoYes.length/(todayStudentReportInfoYes.length+todayStudentReportInfoNo.length))"
                         :percentage="parseInt(100*todayStudentReportInfoYes.length/(todayStudentReportInfoYes.length+todayStudentReportInfoNo.length))">
            </el-progress>
          </span>
          <span style="display: flex;margin-bottom: 10px">
             <el-button style="height: 40px;margin-right : 10%;background-color: red"
                        type="primary" @click="directToClassInfo('','')">{{todayStudentReportInfoNo.length}}
            </el-button>

            <el-progress :stroke-width="7"
                         type="circle"
                         :width="70"
                         :height="70"
                         style="margin-right: 10%"
                         :percentage="parseInt(100*todayStudentReportInfoNo.length/(todayStudentReportInfoYes.length+todayStudentReportInfoNo.length))"
                         v-if="parseInt(100*todayStudentReportInfoNo.length/(todayStudentReportInfoYes.length+todayStudentReportInfoNo.length))"
                         color="red">
            </el-progress>
          </span>
          <div  v-for="item in selectClasses" v-show="item.classId!=''" >
            <span style="display: flex;margin-bottom: 50px">

              <el-button style="height: 40px;margin-right : 10%;"
                         type="primary" @click="directToClassInfo(item.classId,'')"> {{item.classId}}{{item.yesNumber}}
              </el-button>
              <el-progress :stroke-width="7"
                           type="circle"
                           :width="70"
                           :height="70"
                           style="margin-right: 10%"
                           v-if="parseInt(100*item.yesNumber/(item.yesNumber+item.noNumber))"
                           :percentage="parseInt(100*item.yesNumber/(item.yesNumber+item.noNumber))">
              </el-progress>
            </span>
            <span style="display: flex;margin-bottom: 50px">
               <el-button style="height: 40px;margin-right : 10%;background-color: red"
                          type="primary" @click="directToClassInfo(item.classId,'')"> {{item.classId}}{{item.noNumber}}
              </el-button>
              <el-progress :stroke-width="7"
                           type="circle"
                           :width="70"
                           :height="70"
                           style="margin-right: 10%"
                           v-if="parseInt(100*item.noNumber/(item.yesNumber+item.noNumber))"
                           :percentage="parseInt(100*item.noNumber/(item.yesNumber+item.noNumber))" color="red">
              </el-progress>
             </span>
          </div>
        </div>
<!--        -->
        <div  v-show="this.showTableName===5" style="width:100% ">
          <el-form >
            <el-form-item label=":" >
              <el-select v-model="todayStudentReportInfoShowType"  style="width: 33%;margin-right: 5%" >
                <el-option v-for="item in this.todayStudentReportInfoShowTypes"
                           :value="item" :key="item">
                  {{item}}
                </el-option>
              </el-select>
              <el-select v-model="selectClass" style="width: 40%;">
                <el-option v-for="item in this.selectClasses"
                           :value="item.classId" :key="item.classId">
                  {{item.classId}}
                </el-option>
              </el-select>
              <el-button type="primary" @click="searchReportInfo()" style="width: 60%"></el-button>
            </el-form-item>
          </el-form>
          <div v-show="this.todayStudentReportInfoShowType!=''">
            <el-table
                :data="todayStudentReportInfo"
                style="width: 100%">
              <el-table-column label=""  width="120">
                <template slot-scope="scope">
                  <el-button
                      size="mini"
                      width="120" @click="warn(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>
              <el-table-column prop="id" label="" width="120"></el-table-column>
              <el-table-column  prop="isConfirm" label="" width="120">
                <template scope="scope">
                  <span  v-if="scope.row.isConfirm===''" style="background-color: red; color: #d7edff" >{{ scope.row.isConfirm }}</span>
                  <span  v-else >{{ scope.row.isConfirm }}</span>
                </template>
              </el-table-column>
              <el-table-column prop="isArea" label="" width="120">
                <template scope="scope">
                  <span  v-if="scope.row.isArea===''" style="background-color: red; color: #d7edff" >{{ scope.row.isArea }}</span>
                  <span  v-else >{{ scope.row.isArea }}</span>
                </template>
              </el-table-column>
              <el-table-column prop="bodyTemperature" label="" width="120">
                <template scope="scope">
                  <span  v-if="scope.row.bodyTemperature>37.3" style="color: red">{{ scope.row.bodyTemperature }}</span>
                  <span  v-else >{{ scope.row.bodyTemperature }}</span>
                </template>
              </el-table-column>
              <el-table-column prop="name" label="" width="120"></el-table-column>
              <el-table-column prop="classId" label="" width="120"></el-table-column>
              <el-table-column prop="phone" label="" width="120"></el-table-column>
            </el-table>
          </div>
          <div v-show="this.todayStudentReportInfoShowType===''">
            <el-table
                :data="todayStudentReportInfo"
                style="width: 100%">
              <div>
              </div>

              <el-table-column label=""  width="120">
                <template slot-scope="scope">
                  <el-button
                      size="mini"
                      width="120" @click="warn(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>
              <el-table-column prop="id" label="" width="120"></el-table-column>
              <el-table-column prop="name" label="" width="120"></el-table-column>
              <el-table-column prop="classId" label="" width="120"></el-table-column>
              <el-table-column prop="phone" label="" width="120"></el-table-column>
            </el-table>
          </div>
        </div>

      </el-main>

    </el-container>
  </el-container>

</template>


<script>
import axios from "axios";

export default {
  name: "Teacher",
  mounted() {
    let that= this;
    this.timer = setInterval(function() {
      that.time = new Date().toLocaleString();
    });
  },
  beforeDestroy: function() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  data() {
    return {
      reportDate: this.dateFormatter(new Date()),
      time: new Date(),
      classID:[],
      todayStudentReportInfoShowTypes:["",""],
      // 
      todayStudentReportInfo:[],
      //
      todayStudentReportInfoYes:[],
      todayStudentReportInfoNo:[],
      todayStudentReportInfoShowType:"",
      selectClass:"",
      selectClasses:[],
      sexs:['',''],
      addresses :['','','','',
        '','','','','','',
        '','','','','','','',
        '','','','','','','',
        '','','','','','',
        '','','',''],
      tableData:[],
      name:this.$route.params.uname,
      dialogVisible: false,
      msg:"",
      showTableName:4,
      ruleForm: {
        address2: '',
        bodyTemperature2:36.5,
        isConfirm2:false,
        isArea2:false,
        date2:this.dateFormatter(new Date()) ,
      },
      displayUserBase:true,
      userBase: {
        name:'',
        birthday:null,
        email:'',
        phone:'',
        sex:'',
        idCardNumber:''
      },
      rules: {
        bodyTemperature2: [
          { required: true, message: '', trigger: 'blur' }
        ],
        date2: [
          {  required: true, message: '', trigger: 'blur' }
        ]
      }
    };

  },
  methods:{
    dateFormatter(str){//yyyy-MM-dd HH-mm-ss
      // var hasTime = arguments[1] != false ? true : false;//falseyyyy-MM-dd
      var d = new Date(str);
      var year = d.getFullYear();
      var month = (d.getMonth()+1)<10 ? '0'+(d.getMonth()+1) : (d.getMonth()+1);
      var day = d.getDate()<10 ? '0'+d.getDate() : d.getDate();
      var hour = d.getHours()<10 ? '0'+d.getHours() : d.getHours();
      var minute = d.getMinutes()<10 ? '0'+d.getMinutes() : d.getMinutes();
      var second = d.getSeconds()<10 ? '0'+d.getSeconds() : d.getSeconds();
      return [year, month, day].join('-');
    },
    // 
    isCardID ( code){
      var city = { 11: "", 12: "", 13: "", 14: "", 15: "", 21: "", 22: "", 23: " ", 31: "", 32: "", 33: "", 34: "", 35: "", 36: "", 37: "", 41: "", 42: " ", 43: "", 44: "", 45: "", 46: "", 50: "", 51: "", 52: "", 53: "", 54: " ", 61: "", 62: "", 63: "", 64: "", 65: "", 71: "", 81: "", 82: "", 91: " " };
      var tip = ""
      var pass = true
      if (!code || !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(code)) {
        tip = ""
        pass = false;
      } else if (!city[code.substr(0, 2)]) {
        tip = ""
        pass = false
      }
      else {
        // 18
        if (code.length === 18) {
          code = code.split('')
          // (aiWi)(mod 11)
          // 
          var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2]
          // 
          var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2]
          var sum = 0
          var ai = 0
          var wi = 0
          for (var i = 0; i < 17; i++) {
            ai = code[i]
            wi = factor[i]
            sum += ai * wi
          }
          var last = parity[sum % 11];
          if (parity[sum % 11] != code[17]) {
            tip = ""
            pass = false
          }
        }
      }

      if (!pass) {
        this.showDialog(""+tip+":"+code);
      }

    },
    // 
    isvalidPhone(str) {
      const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
      let string =reg.test(str);
      if (string!==true){
        this.showDialog(""+str);
      }

    },
    directToClassInfo(clas,type){
      // console.log(clas);
      this.selectClass=clas;
      // console.log(this.selectClass);
      this.showTableName=5;
      this.todayStudentReportInfoShowType=type;
      this.searchReportInfo();
    },
    datePick(){
      // this.teacherStudentTodayReportInfo();
      // this.selectClass='';
      // this.todayStudentReportInfoShowType='';
      this.searchReportInfo();
    },

    warn(a,b){
      var that=this;
      axios.post('/warnReport'
          +"?id="+b.id
      ).then(response=>{
        console.log(a);
        console.log(b.id);
        console.log(response);
        if (response.data.message===''){
          that.showDialog("");
        }else{
          that.showDialog("");
        }
      })
          .catch(error=> {
            console.log(error);
            that.showDialog("");
          });

    },
    //
    searchReportInfo(){
      this.teacherStudentTodayReportInfo();
      if (this.selectClass==""){
        if (this.todayStudentReportInfoShowType==""){
          this.todayStudentReportInfo=this.todayStudentReportInfoNo;
        }
        else  if (this.todayStudentReportInfoShowType==""){
          this.todayStudentReportInfo=this.todayStudentReportInfoYes;
        }
      }
      else {
        var a;
        if (this.todayStudentReportInfoShowType==""){
          a=this.todayStudentReportInfoNo;
        }
        else  if (this.todayStudentReportInfoShowType==""){
          a=this.todayStudentReportInfoYes;
        }
        for (var i=a.length-1;i>=0;i--){
          var student=a[i];
          if (student.classId!=this.selectClass)
            a.splice(i,1);
        }
        // console.log(a);
        this.todayStudentReportInfo=a;
      }
    },
    //
    teacherStudentTodayReportInfo(){
      var that=this;
      axios.post('/teacherStudentTodayReportInfo'+
          "?id="+this.name+"&date="+this.reportDate)
          .then(response=>{
            console.log(response);
            // console.log(response.data.noReportStudent);
            // this.todayStudentReportInfo=response.data.noReportStudent;
            this.todayStudentReportInfoYes=response.data.reportStudent;
            this.todayStudentReportInfoNo=response.data.noReportStudent;
            this.selectClasses=response.data.teacherAllClass;
            // console.log(this.selectClasses);
            // this.classID=this.selectClasses.splice(this.selectClasses.length-1,1);
          })
          .catch(error=> {
            console.log(error);
            // that.showDialog("");
          });
    },
    getUserBaseInfo(){
      var that=this;
      axios.post('/getUserBaseInfo'+
          "?id="+this.name+
          "&type=teacher")
          .then(response=>{
            // console.log(response);
            // console.log(response.data.getUserBaseInfo[0]);
            var s=response.data;
            that.userBase.name=s.name;
            if (s.birthday==="undefined"){
              that.userBase.birthday=null;
            }
            else {
              that.userBase.birthday=s.birthday;
            }
            // that.userBase.birthday=s.birthday;
            that.userBase.email=s.email;
            that.userBase.phone=s.phone;
            that.userBase.sex=s.sex;
            that.userBase.idCardNumber=s.idCardNumber;

          })
          .catch(error=> {
            console.log(error);
            that.showDialog("")
          });
    },
    submitUserBaseInfo(form){
      var that=this;
      this.displayUserBase=true;
      axios.post('/submitUserBaseInfo'+
          "?id="+this.name+
          "&name="+form.name+
          "&birthday="+form.birthday+
          "&email="+form.email+
          "&phone="+form.phone+
          "&sex="+form.sex+
          "&idCardNumber="+form.idCardNumber+
          "&type=teacher")
          .then(response=>{
            console.log(response);
            // console.log(response.data.message);
            if(response.data.message==='true'){
              that.showDialog("");
            }
            else if (response.data.message==='false'){
              that.showDialog("");
            }
          })
          .catch(error=> {
            console.log(error);
            that.showDialog("")
          });
    },
    showDialog(msg){
      this.dialogVisible=true;
      this.msg=msg;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var that=this;
          axios.post('/setReportInfo?reportDate='+this.ruleForm.date2+
              "&bodyTemperature="+this.ruleForm.bodyTemperature2+
              "&isConfirm="+this.ruleForm.isConfirm2+
              "&isArea="+this.ruleForm.isArea2+
              "&address="+this.ruleForm.address2+
              "&uameId="+this.name)
              .then(response=>{
                console.log(response);
                // console.log(response.data.message);
                if(response.data.message==='true'){
                  that.showDialog("");
                  this.getStudentReportHistory(this.name);
                }
                else if (response.data.message==='false'){
                  that.showDialog("");
                }
                else if (response.data.message===''){
                  that.showDialog("");
                  this.getStudentReportHistory(this.name);
                }
                else if (response.data.message===''){
                  that.showDialog("");
                }

              })
              .catch(error=> {
                console.log(error);
                that.showDialog("")
              });
        } else {
          console.log('error submit!!');
          this.showDialog("");
          return false;
        }
      });

    },
    handleDelete(index, row) {
      // console.log(index, row);
      var that=this;
      // console.log( row.uameId);
      // console.log( row.reportDate);
      axios.post('/deleteStudentReport?uameId='+row.uameId+"&reportDate="+row.reportDate)
          .then(response=>{
            // console.log(response);
            // console.log(response.data.message);
            if(response.data.message===true){
              that.showDialog("");
              that.getStudentReportHistory(row.uameId);
            }
            else if (response.data.message===false){
              that.showDialog("");
              // console.log("");
            }
          })
          .catch(error=> {
            console.log(error);
            that.showDialog(",")
          });


    },
    handleClose(done) {
      this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
    },
    toLogin(){
      this.$router.push('/');
    },
    showWitch(e){
      this.showTableName=e;
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getStudentReportHistory(name){
      var that=this;
      axios.post('/getStudentReportHistory?uname='+name)
          .then(response=>{
            console.log(response);
            var a=response.data.getStudentReportHistory;
            that.tableData=a;
          })
          .catch(error=> {
            console.log(error);
            that.showDialog(",")
          });
    }
  },
  created() {
    if (this.$route.params.uname===undefined){
      this.$router.push('/');
    }
    this.name= this.$route.params.uname;
    this.getStudentReportHistory(this.name);
    this.getUserBaseInfo();
    this.teacherStudentTodayReportInfo();
    this.todayStudentReportInfo=this.todayStudentReportInfoNo;
    // console.log("");
  },
}
</script>

